.delete-btn {
  float: right;
  color: #e74944;
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

.wrapper {
  padding-top: 50px;
  background: whitesmoke;
  height: 100%;
  width: 100%;
  text-align: center;
  display: flex;
  flex-flow: row wrap;
  padding: 30px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

